body{
	background: #f2f2f2;
	font-family: 'PingFang SC', 'Myriad Pro', 'Hiragino Sans GB', 'microsoft yahei';
    -webkit-font-smoothing: antialiased;
}
section,header,footer{
	padding:0px;margin:0px;display: block;
}
a{
	font-size:14px;
	line-height:18px;
	color:#999;
}
img{
	border:none;
}
.hide{
	display:none;
}
.app-nav-mod{
	margin-bottom:10px;
	// padding:0 18px;
	background: #fff;
	overflow: hidden;
	.navOld{
	    padding: 17px 0 14px;
	    display: -webkit-box;
	    display: -moz-box;
	    display: box;
	    overflow: hidden;
	    li{
    	    -webkit-box-flex: 1;
		    -moz-box-flex: 1;
		    box-flex: 1;
		    text-align: center;
		    a{
		    	margin:0 auto;
		    	width:40px;
	    	    font-size: 14px;
			    line-height: 18px;
			    color: #999;
			    text-align:center;
			    img{
			    	width:40px;
			    	height:40px;
			    }
			    em{
			    	display:block;
			    	margin:0 auto;
			    	width:30px;
			    	height:30px;
			    	background: url(../../img/wx/laikan/icon/icon-index-nav.png?v=2) 0 0 no-repeat;
				    -webkit-background-size: 100px auto;
				    background-size: 100px auto;
				    background-position:0 -50px;
				    &.female{
						background-position:0 -100px;
				    }
				    &.free{
				    	background-position:-50px -150px;
				    }
				    &.rank{
				    	background-position:0 -250px;
				    }
				    &.category{
				    	background-position:0 0;
				    }
			    }
			    span{
			    	padding-top:5px;
		    	    display: block;
				    font-size: 14px;
				    color: #777;
			    }
		    }
	    }
	}
	.nav-channel{
		padding: 0 15px 15px;
		overflow: hidden;
		li{
		  padding-top:15px;
		  width:25%;
		  float:left;
		  a{
		    display:-webkit-box;
		    display:-moz-box;
		    display:box;
		    font-size:14px;
		    margin: 0 auto;
		    width:70px;
		  }
		  span{
		    display:block;
		    &.text{
		      height:30px;
		      line-height:30px;
		      -webkit-box-flex:1;
		      -moz-box-flex:1;
		      box-flex:1;
		      color:#333;
		    }
		    &.icon{
			    margin-right:5px;
			    width: 30px;
			    height: 30px;
			    background: url(../../img/wx/laikan/icon/icon-index-nav.png?v=2) 0 0 no-repeat;
			    -webkit-background-size: 100px auto;
			    background-size: 100px auto;
			}
		  }
		}
	}
	.freeMod{
		padding:10px 18px;
		display:-webkit-box;
		display:-moz-box;
		display: box;
		border-top:solid 1px #e5e5df;
		overflow: hidden;
		.flexDiv{
			-webkit-box-flex:1;
			-moz-box-flex:1;
			box-flex:1;
			// margin: 0 1.5%;
			height:38px;
			line-height:38px;
			color:#fff;
			font-size:1.4rem;
			text-align: center;
			border-radius: 4px;
			background:#85b4d9;
			// img{
			// 	display:inline-block;
			// 	vertical-align: -3px;
			// 	margin-right:5px;
			// 	height:18px;
			// }
			span{
				margin-right:5px;
				display:inline-block;
				width:17px;
				height:17px;
				background: url(../../img/app/app_bg.png?v=3) 0 0 no-repeat;
			    -webkit-background-size: 50px auto;
			    background-size: 50px auto;
			    vertical-align: -4px;
			}
			&.free{
				background: #8ed2e6;
				span{
					background-position:0 -33px;
				}
			}
			&.recommend,&.topic{
				margin-left:10px;
				background: #d3b2f4;
				span{
					background-position:0 -55px;
				}
			}
			&.topic{
				span{
					background-position:0 -100px;
				}
			}
			&.package{
				margin-left:10px;
				background-color: #a6e084;
				span{
					background-position:0 -78px;
				}
			}
		}
	}
}

.app-nav-mod .nav-channel li span.icon-male{
    background-position:0 -50px;
}
.app-nav-mod .nav-channel li span.icon-female{
    background-position:0 -100px;
}
.app-nav-mod .nav-channel li span.icon-free{
    background-position:0 -150px;
}
.app-nav-mod .nav-channel li span.icon-best{
    background-position:-50px -150px;
}
.app-nav-mod .nav-channel li span.icon-direct{
    background-position:0 -350px;
} 
.app-nav-mod .nav-channel li span.icon-rank{
    background-position:0 -250px;
}
.app-nav-mod .nav-channel li span.icon-category{
    background-position:0 0;
}
.app-nav-mod .nav-channel li span.icon-monthly{
    background-position:-50px -100px; 
}

.app-ban-mod{
	position: relative;
	width: 100%;
	min-height:100px;
	overflow: hidden;
	img{
		display: block;
		width:100%;
		// width:auto !important;
		// height:130px;
	}
	.banBd li{
		&.show{
			display:block;
		}
		&.hide{
			display: none;
		}
	}
	.banHd{
		position: absolute;
		right: 10px;
		bottom: 5px;
		li{
			float: left;
			width: 8px;
			height: 8px;
			border-radius: 10px;
			text-align: center;
			color: #333;
			background: #fff;
			margin-left: 4px;
			cursor: pointer;
			text-indent: -9999px;
			&.on{
				color: #fff;
				background: #46a0f6;
			}
		}
	}
}
.app-note-mod{
	position: relative;
	padding:0 18px;
	height:38px;
	font-size:14px;
	background:#fff;
	border-bottom: solid 1px #dedede;
	overflow:hidden;
	div{
		height:38px;
		line-height:38px;
		overflow:hidden;
		li{
			padding-left:25px;
			height:38px;
			line-height:38px;
			white-space:nowrap;overflow:hidden;text-overflow:ellipsis;
		}
	}
	span{
		position:absolute;
		top:0;
		left:18px;
		width:20px;
		display:block;
		img{
			width:20px;
			vertical-align: -5px;
		}
	}
}
.app-mod{
	margin-bottom:10px;
	background:#fff;
	overflow:hidden;
	h3{
		position:relative;
		margin:17px 18px 15px;
		font-size:16px;
		color:#333;
		line-height:0;
		img{
			width:3px;
			height:16px;
			vertical-align:-2px;
			margin-right:8px;
		}
		span{
			position:absolute;
			right:0;
			top:9px;
			display:block;
			font-size:14px;
			color:#919088;
			font-weight:normal;
			&.changeIco{
				top:1px;
				color: #10c1ae !important;
			}
			img{
				display:inline-block;
				width:13px;
				height:13px;
				vertical-align:-2px;
				margin:0 0 0 3px;
			}
			b{
				font-weight:normal;
				font-size:14px;
			}
		}
	}
	.bd ul{
		padding:0 18px 14px;
		display:none;
		font-size:14px;
		line-height:24px;
		&.show{
			display:block;
		}
		&.mg10{
			padding-top:10px;
		}
		li{
			display: -webkit-box;
			display:-moz-box;
			display:box;
			margin-bottom:20px;
			&:last-child{
				margin-bottom:0;
			}
			a{
				color:#333;
				font-size:16px;
				&.l{
					display:block;
					padding:0 5px;
					margin:-2px 5px 0 0;
					height:20px;line-height:20px;
					color:#10c1ae;
					font-size:14px;
					text-align: center;
					border-radius: 4px;
					border:solid 1px #95d6cb;
				}
				&.r{
					-webkit-box-flex:1.0;
					-moz-box-flex:1.0;
					box-flex:1.0;
					height:17px;
					display:block;
					overflow: hidden;
				}
				&.num{
					margin:0 5px 0 0;
					display:block;
					width:18px;
					height:18px;
					line-height:18px;
					font-size:12px;
					text-align:center;
					color:#fff;
					background:#fd6041;
					border-radius:0 0 8px 0;
				}
				&.numY{
					background:#ff9a24;
				}
			}
		}
	}
}
.app-dl-mod{
	dl{
		margin:0 18px 15px;
		padding-bottom:15px;
		display:-webkit-box;
		display:-moz-box;
	    display:box;
	    border-bottom:solid 1px #e5e5e5;
		overflow:hidden;
		&:last-child{
			border-bottom:none;
			padding-bottom:5px;
		}
		&.bor{
			border-bottom:solid 1px #e5e5e5;
			padding-bottom:12px;
			margin-bottom:5px;
		}
		dt{
			position: relative;
			margin-right:10px;
			img{
				width:85px;
				display:block;
			}
			span{
				position:absolute;
				top:0;
				left:0;
				width:19px;
				height:19px;
				line-height:19px;
				color:#fff;
				text-align:center;
				font-size:12px;
				border-radius:0 0 8px 0;
				background:#e83c3c;
			}
		}
		dd{
			-webkit-box-flex:1.0;
			-moz-box-flex:1.0;
			box-flex:1.0;
			div{
				height:92px;
				&.ht{
					height:70px;
				}
			}
			span{
				margin:4px 0 12px;
				display:block;
				font-size:16px;
				line-height:20px;
				color:#333;
				overflow:hidden;
			}
			b{
				margin-bottom:5px;
				display:block;
				font-weight:normal;
				font-size:14px;
				height:44px;
				line-height:22px;
				overflow:hidden;
			}
			p{
				position: relative;
				height:22px;
				line-height:22px;
				font-size:14px;
				color:#999;
				img{
				    width: 15px;
				    vertical-align: -2px;
				    margin-right: 5px;  
				}
				span{
		    	    position: absolute;
				    top:0px;
				    right: 0;
				    display: block;
				    margin: 0;
				    line-height: 12px;
				    height: auto;
				    a{
				    	display:inline-block;
				    	i{
				    	    margin-left: 6px;
						    display: inline-block;
						    border: solid 1px #d7d7d7;
						    border-radius: 2px;
						    padding: 4px 4px 2px;
						    font-size: 12px;
					    	line-height: 12px;
						    color: #999;
					    }
				    }  
			    }
			}
		}
	}	
}
.app-sale-mod{
	padding:0;
	margin-bottom:10px;
	background:#fff;
	overflow:hidden;
	h3{
		position:relative;
		margin:17px 18px 15px;
		font-size:16px;
		color:#333;
		line-height:0;
		img{
			width:3px;
			height:16px;
			vertical-align: -2px;
			margin-right:8px;
		}
		span{
			position:absolute;
			right:0;
			top:9px;
			display:block;
			font-size:14px;
			color:#666;
			font-weight:normal;
			b{
				font-weight:normal;
				display:block;float:left;background:#f55;
				color:#fff;width:20px;height:20px;line-height:20px;
				text-align:center;border-radius:4px;
			}
			i{
				float:left;margin:0 5px;display:block;color:#f55;font-style:normal;line-height:20px;
			}
		}
	}
	ul{
		padding:0 18px;
		width:100%;
		display:none;
		overflow:hidden;
		&.show{
			display:block;
		}
		li{
			position: relative;
			margin-right:5%;
			width:30%;
			float:left;
			a{
				display:block;
				width:100%;
				img{
					width:100%;
					display:block;
				}
				p{
					margin-top:9px;
					font-size:14px;
					max-height:40px;
					line-height:20px;
					color:#757575;
					overflow:hidden;
				}	
			}
			&:last-child{
				margin-right:0;
			}
			span{
				position:absolute;
				right:0;
				top:0;
				img{
					width:40px;
				}
			}
		}
	}
}
