					background-size:50px auto;
					background-position:0 -18px;
					vertical-align:-2px;
				}
			}
	    }
		//左文右图
		&.type1,.hasPic{
			display: -webkit-box;
		    display: -moz-box;
		    display: box;
		    .leftCol{
				-webkit-box-flex: 1;
			    -moz-box-flex: 1;
			    box-flex: 1;
				// h2{
				// 	height:55px;
				// }
		    }
		    .rightCol{
		    	margin-left:15px;
		    	width:100px;
		    	height:75px;
		    	overflow:hidden;
		    	img{
		    		width:100%;
		    		display:block;
		    	}
		    }
		}

		//上文下图
		&.type2{
			h2{
				margin:0 0 10px 0;
			}
			.picCont{
			    margin-bottom: 10px;
			    position: relative;
				height: 120px;
				.picBg{
					position: absolute;
					width:100%;
					height:100%;
				}
			}
			
			.picCol{
				width:100%;
				display:block;
				margin-bottom:10px;
				overflow:hidden;
			}
		}
		//左文右图second
		&.type3{
			h2{
				height:auto !important;
				padding-bottom:10px;
			    white-space: nowrap;
			    overflow: hidden;
			    text-overflow: ellipsis;
			}
			.derictText{
				color:#999;
				font-size:14px;
				line-height:20px;
				height:40px;
				overflow: hidden;
			}
			.bookOther{
				margin-top:12px;
			}
		}
		&.type4{
			h2{
				margin:0 0 10px 0;
			}
		}
	}
}



